import { Injectable } from "@angular/core";

@Injectable({
	providedIn: "root",
})
export class GroupByService {
	constructor() {}

	//group response data by DayName
	static groupBy(collection: any[], property: string): any[] {
		if (!collection) {
			// return null;
		}
		const groupedCollection = collection.reduce((previous, current) => {
			if (!previous[current[property]]) {
				previous[current[property]] = [current];
			} else {
				previous[current[property]].push(current);
			}
			return previous;
		}, {});
		return Object.keys(groupedCollection).map((key) => ({
			key,
			List: groupedCollection[key],
		}));
	}

	groupByPartOfProperty(collection: any[], property: string, noOfLetters: number): any[] {
		const groupedCollection = collection.reduce((previous, current) => {
			let searchKey = current[property].trim().slice(0, noOfLetters).toUpperCase();
			if (!previous[searchKey]) previous[searchKey] = [current];
			else previous[searchKey].push(current);
			return previous;
		}, {});
		return Object.keys(groupedCollection).map((key) => ({
			key,
			List: groupedCollection[key],
		}));
	}
}
