import { Injectable } from "@angular/core";
import { IEmployeeCardModel } from "app/personnel-setup/models/employees";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ResourcesChartService extends AppClientService {
  constructor() {
    super();
  }

  getEmployeesChart(employeeId: number) {
    return this.get<IResponse<IEmployeeCardModel[]>>(
      "personnel/Employees/GetAll_EmployeesCard_Structure",
      {
        params: {
          PerinsID: employeeId,
        },
      }
    );
  }
}
