import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { OrganizationsComponent } from "app/org-management-setup/components/organizations/organizations.component";
import { IOrganizationModel } from "app/org-management-setup/models/organizations";
import { OrganizationsService } from "app/org-management-setup/services/organizations.service";
import {
  IOrgStructureNodeModel,
  IOrgStructureNodeRequest,
} from "app/org-management/model/org-structure";
import { OrgStructureService } from "app/org-management/services/org-structure.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-add-org-structure-node",
  templateUrl: "./add-org-structure-node.component.html",
  styleUrls: ["./add-org-structure-node.component.scss"],
})
export class AddOrgStructureNodeComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  // organization dropdown
  organizationList: IOrganizationModel[] = [];
  viewName: string;

  constructor(
    private orgStructureService: OrgStructureService,
    private organizationsService: OrganizationsService,
    public dialogRef: MatDialogRef<AddOrgStructureNodeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    super();
    // console.log(data);
    this.viewName = ViewsManager.organizations;
  }

  ngOnInit() {
    this.form = this.fb.group({
      // oldId: [0, { nonNullable: true }],
      org_Structure_ID: [null],
      parenT_ID: [null],
      orG_TYPE_ID: [null, [Validators.required]],
      orG_ID: [null, [Validators.required]],
    });

    this.getFormValues();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (
      this.formCtrls.org_Structure_ID.value > 0 &&
      this.checkNoBusinessOrCompany()
    ) {
      this.getAllUnAssignedOrganizationsByStructureId();
    }
  }

  getFormValues() {
    this.form.patchValue(this.data);
    // // save old org id to edit
    // this.formCtrls.oldId.setValue(this.data.orG_ID);
    setTimeout(() => {
      // this.formCtrls.orG_TYPE_ID.setValue(this.data.orG_TYPE_ID);
      if (!this.form.value.parenT_ID) {
        this.formCtrls.orG_TYPE_ID.disable();
        this.formCtrls.orG_TYPE_ID.setValue(1);
      }
      // this.formCtrls.orG_ID.setValue(this.data.orG_ID);
    }, 100);
    // console.log(this.data, this.form.value);
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IOrgStructureNodeRequest = {
        core_Org_Structure_Org: {
          org_Structure_ID: this.formCtrls.org_Structure_ID.value,
          orG_TYPE_ID: this.formCtrls.orG_TYPE_ID.value,
          orG_ID: this.formCtrls.orG_ID.value,
          parenT_ID: this.formCtrls.parenT_ID.value,
        },
      };

      // this.form.value.oldId == 0 ? this.create(model) : this.update(model);
      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IOrgStructureNodeRequest) {
    this.showLoading();
    this.orgStructureService
      .createNode(model)
      .subscribe((res: IResponse<IOrgStructureNodeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.dialogRef.close(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  // update(model: IOrgStructureNodeRequest) {
  //   model.oldOrgID = this.form.value.oldId;
  //   this.showLoading();
  //   this.orgStructureService
  //     .updateNode(model)
  //     .subscribe((res: IResponse<IOrgStructureNodeModel>) => {
  //       if (res.message.messageType == MessageTypes.Success) {
  //         this.updateSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.dialogRef.close(true);
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  // organization list
  getAllUnAssignedOrganizationsByStructureId() {
    this.showLoading();
    this.organizationsService
      .GetAllUnAssignedByStructureId(this.formCtrls.org_Structure_ID.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrganizationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.organizationList = res.data;
          // if (this.formCtrls.oldId.value > 0)
          //   this.organizationList.unshift(this.data.currentOrganization);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(OrganizationsComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllUnAssignedOrganizationsByStructureId();
      });
  }
}
