import {Component, HostListener, OnInit, ViewChild} from "@angular/core";
import {DialogPosition, MatDialog} from "@angular/material/dialog";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {TreeNode} from "primeng/api";
import {FormGroup} from "@angular/forms";
import {Idropdown} from "app/shared/interfaces/dropdown.interface";
import {IJobTitleModel} from "app/org-management/model/job-title";
import {EmployeeCardComponent} from "../employee-card/employee-card.component";
import {JobTitleDetailsDialogComponent} from "../job-title-details-dialog/job-title-details-dialog.component";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {JobTitleChartService} from "app/org-management/services/job-title-chart.service";
import {
    EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import {IEmpJobDataModel} from "app/personnel-setup/models/employee-employment-info";
import {jobTitleChartToMap} from "app/org-management/mapper/org-structure";
import {OrganizationChart} from "primeng/organizationchart";
import {exportChart} from "../../../export_chart";

@Component({
    selector: "app-job-title-org-chart",
    templateUrl: "./job-title-org-chart.component.html",
    styleUrls: ["./job-title-org-chart.component.scss"],
})
export class JobTitleOrgChartComponent extends AppHelpers implements OnInit {
    // all structure hierarchy
    jobTitleOrgChart: TreeNode<IJobTitleModel>[];

    form: FormGroup;
    loading: boolean = false;
    lastSelectedJobId: number = 0;

    searchTypeList: Idropdown[] = [];
    employeeJobTitles: IEmpJobDataModel[] = [];
    @ViewChild("jobTitleChart") jobTitleChart: OrganizationChart;

    constructor(
        private jobTitleChartService: JobTitleChartService,
        private employeeJobInformationService: EmployeeJobInformationService,
        private dialog: MatDialog
    ) {
        super();
    }

    ngOnInit(): void {
        this.form = this.fb.group({
            searchTypeId: [null],
            jobTitleId: [null],
            employeeId: [null],
        });

        setTimeout(() => {
            this.searchTypeList = [
                {
                    id: 1,
                    ar_Name: "الوظائف",
                    en_Name: "Job titles",
                },
                {
                    id: 2,
                    ar_Name: "الموظفين",
                    en_Name: "Employees",
                },
            ];
        }, 100);
    }

    override onChangeBusinessAndCompanyId(): void {
        this.jobTitleOrgChart = null;
        if (this.checkNoBusinessOrCompany())
            this.getJobTitleOrgChart(this.formCtrls.jobTitleId.value);
    }

    get formCtrls() {
        return this.form.controls;
    }

    getJobTitleOrgChart(jobTitleId: number) {
        // console.log(this.form.value);
        if (jobTitleId > 0) {
            this.lastSelectedJobId = jobTitleId;
            this.jobTitleOrgChart = null;
            this.loading = true;
            this.jobTitleChartService
                .getJobTitleHierarchy(jobTitleId)
                .pipe(this.cancelRequest())
                .subscribe((res: IResponse<IJobTitleModel[]>) => {
                    if (res.message.messageType == MessageTypes.Success) {
                        this.jobTitleOrgChart = jobTitleChartToMap(res.data);
                        setTimeout(() => {
                            this.setPositionBtnUpOneLevel();
                            this.setPositionUpOneLevelLine();
                        }, 100);
                        if (this.lastSelectedJobId == jobTitleId) this.loading = false;
                    } else {
                        this.errorList(res.message.messages);
                        this.loading = false;
                    }
                });
        }
    }

    onChangeSearchType() {
        // console.log("change type");
        this.formCtrls.jobTitleId.reset();
        this.formCtrls.employeeId.reset();
    }

    // on select job title or employee to start show from it
    onChangeSearchValue() {
        this.getJobTitleOrgChart(this.formCtrls.jobTitleId.value);
    }

    // up one level in chart
    upOneLevel() {
        this.onChangeSearchType();
        // get job titles start from parent job title id
        this.formCtrls.jobTitleId.setValue(
            this.jobTitleOrgChart[0].data.parentJobTitleID
        );
        // this.getJobTitleOrgChart(this.jobTitleOrgChart[0].data.parentJobTitleID);
    }

    upOneLevelBtnPositions = {
        btnLeft: 0,
        lineLeft: 0,
    };

    setPositionBtnUpOneLevel() {
        let btnWidth = document
            .querySelector(".upOneLevel-btn")
            .getBoundingClientRect().width;
        let firstNodePositions = document
            .querySelector(".p-organizationchart-node-content")
            ?.getBoundingClientRect();
        let firstNodeContainerPositions = document
            .querySelector(".p-organizationchart")
            ?.getBoundingClientRect();

        this.upOneLevelBtnPositions.btnLeft =
            firstNodePositions?.left -
            firstNodeContainerPositions?.left +
            firstNodePositions?.width / 2 -
            btnWidth / 2 ?? 0;
    }

    setPositionUpOneLevelLine() {
        let firstNodePositions = document
            .querySelector(".p-organizationchart-node-content")
            ?.getBoundingClientRect();
        let firstNodeContainerPositions = document
            .querySelector(".p-organizationchart")
            ?.getBoundingClientRect();

        this.upOneLevelBtnPositions.lineLeft =
            firstNodePositions?.left -
            firstNodeContainerPositions?.left +
            firstNodePositions?.width / 2 ?? 0;
    }

    // node details dialog
    openJobTitleDetailsDialog(
        target: HTMLElement,
        nodeData: IJobTitleModel,
        expandedIndex: number
    ) {
        let targetNode: HTMLElement = this.getHeaderElement(target);
        let targetPositions: DOMRect = targetNode.getBoundingClientRect();

        let dialogWidth = 350;
        let dialogPositions: DialogPosition = {};
        // can open left
        if (
            targetPositions.left + targetPositions.width + 10 + dialogWidth <=
            window.innerWidth
        )
            dialogPositions.left = `${
                targetPositions.left + targetPositions.width + 10
            }px`;
        // can open right
        else if (targetPositions.left - dialogWidth - 10 >= 0)
            dialogPositions.left = `${targetPositions.left - dialogWidth - 10}px`;
        // no left and no right
        else dialogPositions.left = null;

        return this.dialog
            .open(JobTitleDetailsDialogComponent, {
                data: {
                    nodeData: nodeData,
                    expandedIndex: expandedIndex,
                },
                position: dialogPositions,
                width: `${dialogWidth}px`,
                // maxWidth: "350px",
                maxHeight: "90hv",
                // disableClose: true,
                autoFocus: false,
            })
            .afterClosed()
            .subscribe((closed: boolean) => {
            });
    }

    getHeaderElement(target: HTMLElement): HTMLElement {
        return target.classList.contains("p-organizationchart-node-content")
            ? target
            : this.getHeaderElement(target.parentElement);
    }

    // expand node
    expandNode(node: TreeNode) {
        node.expanded = !node.expanded;
    }

    // // assign employee to job title
    // openAssignDialog(nodeData: IJobTitleModel) {
    //   return this.dialog
    //     .open(AssignEmployeeToJobTitleComponent, {
    //       data: {
    //         nodeData: nodeData,
    //       },
    //       minWidth: "50vw",
    //       autoFocus: false,
    //     })
    //     .afterClosed()
    //     .subscribe((updated: boolean) => {
    //       if (updated) {
    //         // to do >> get first node job title id
    //         this.getJobTitleOrgChart(this.jobTitleOrgChart[0].data.id);
    //       }
    //     });
    // }

    // open employee card dialog
    openEmployeeCardDialog(target: HTMLElement, employeeId: number) {
        let lastTarget: HTMLElement = this.getHeaderElement(target);
        let targetPositions: DOMRect = lastTarget.getBoundingClientRect();

        // dialog position x-axis (right or left)
        let dialogWidth = 350;
        let dialogPositions: DialogPosition = {};
        // can open left
        if (
            targetPositions.left + targetPositions.width / 2 + dialogWidth <=
            window.innerWidth
        )
            dialogPositions.left = `${
                targetPositions.left + targetPositions.width / 2
            }px`;
        // can open right
        else if (
            targetPositions.left - dialogWidth / 2 - targetPositions.width / 2 + 10 >=
            0
        )
            dialogPositions.left = `${
                targetPositions.left - dialogWidth / 2 - targetPositions.width / 2 + 10
            }px`;
        // no left and no right
        else dialogPositions.left = null;

        return this.dialog
            .open(EmployeeCardComponent, {
                data: {employeeId: employeeId},
                position: dialogPositions,
                width: `${dialogWidth}px`,
                // maxWidth: "350px",
                maxHeight: "80hv",
                autoFocus: false,
            })
            .afterClosed()
            .subscribe((closed: boolean) => {
            });
    }


    // change up one level btn & line positions
    @HostListener("window:resize", ["$event"])
    onWindowResize(event) {
        // this.dialog.closeAll();
        this.setPositionBtnUpOneLevel();
        this.setPositionUpOneLevelLine();
    }

    // displayAssignEmployeeIcon(jobTitle: IJobTitleModel): boolean {
    //   let display = false;
    //   jobTitle.core_Org_Job_Title.map((item) => {
    //     if (
    //       item.max_Employee == 0 ||
    //       item.currentEmployeeNumber <= item.max_Employee
    //     )
    //       display = true;
    //   });
    //   return display;
    // }

    getAllEmployeeJobTitles() {
        let employeeId: number = this.formCtrls.employeeId.value;
        if (employeeId > 0) {
            this.showLoading();
            this.employeeJobInformationService
                .getAllCurrentJobsByEmployeeId(employeeId)
                .pipe(this.cancelRequest())
                .subscribe((res: IResponse<IEmpJobDataModel[]>) => {
                    if (res.message.messageType == MessageTypes.Success) {
                        this.employeeJobTitles = res.data;
                    } else this.errorList(res.message.messages);
                    this.hideLoading();
                });
        }
    }

    protected readonly exportChart = exportChart;
}
