import {Component, HostListener, Inject, OnInit,} from "@angular/core";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {employeeToMap} from "app/personnel-setup/mapper/employee";
import {IEmployeeCardModel} from "app/personnel-setup/models/employees";
import {EmployeesService} from "app/personnel-setup/services/employees/employees.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
  selector: "app-employee-card",
  templateUrl: "./employee-card.component.html",
  styleUrls: ["./employee-card.component.scss"],
})
export class EmployeeCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  employeeData: IEmployeeCardModel;
  loaded: boolean = false;
  showMainData: boolean = false;

  constructor(
    private employeesService: EmployeesService,
    public dialogRef: MatDialogRef<EmployeeCardComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    super();
    if (data) {
      this.employeeId = data.employeeId;
      this.showMainData = data.showMainData ?? false;
    }
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany() && this.employeeId > 0) this.getEmployeeData();

    const overlays = document.querySelectorAll(".cdk-overlay-backdrop");
    if (overlays.length > 0)
      overlays[overlays.length - 1].classList.add("no-overlay");
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.employeeId > 0)
      this.getEmployeeData();
  }

  getEmployeeData() {
    this.employeesService
      .getEmployeeCardById(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeCardModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length > 0) {
            this.employeeData = employeeToMap(res.data)[0];
          }
          this.loaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  // close dilog on resize screen
  @HostListener("window:resize", ["$event"])
  onWindowResize(event) {
    this.dialogRef.close();
  }
}
