import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  ICompanyNodeChartData,
  IOrgStructureModel,
  IOrgStructureNodeModel,
} from "../model/org-structure";
import { IJobTitleModel } from "../model/job-title";
import { TreeNode } from "primeng/api";
import { IEmployeeCardModel } from "app/personnel-setup/models/employees";

export function orgStructureToMap(
  orgStructures: IOrgStructureModel[]
): IOrgStructureModel[] {
  return orgStructures.map((orgStructure) => {
    return <IOrgStructureModel>{
      ...orgStructure,
      froM_DATE: HelpersFunctions.mapDate(orgStructure.froM_DATE),
      tO_DATE: HelpersFunctions.mapDate(orgStructure.tO_DATE),
    };
  });
}

/* /////////////////// company structure chart /////////////////// */

export function companyChartToMap(
  StructureHierarchy: IOrgStructureModel,
  orgStructureId: number
): TreeNode<ICompanyNodeChartData>[] {
  // sort nodes
  let selectedIds: number[] = [];
  let sortedOrgNodes: IOrgStructureNodeModel[] = [];
  while (
    sortedOrgNodes.length < StructureHierarchy.core_Org_Structure_Org.length
  ) {
    companySortDataBeforeMap(
      StructureHierarchy.core_Org_Structure_Org,
      sortedOrgNodes,
      selectedIds
    );
  }
  // console.log(selectedOrgNodes, selectedIds);

  // all structure hierarchy
  let orgStructureDetails: TreeNode<ICompanyNodeChartData>[];

  orgStructureDetails = [
    {
      label: "structure",
      type: "org",
      styleClass: "structure-node",
      expanded: true,
      expandedIcon: "pi pi-folder-open",
      collapsedIcon: "pi pi-folder",
      data: {
        parentId: null,
        org: null,
        orgType: null,
        numEmp: 0,
        structure: {
          id: null,
          ar_Name: StructureHierarchy.ar_Name,
          en_Name: StructureHierarchy.en_Name,
        },
      },
      children: [],
    },
  ];

  // maping data
  sortedOrgNodes.map((item) => {
    let mapedItem: TreeNode<ICompanyNodeChartData> = {
      label: `${item.core_Org?.code} - ${msgTranslate(
        item.core_Org?.ar_Name,
        item.core_Org?.en_Name
      )}`,
      type: "org",
      styleClass: "org-node",
      expanded: true,
      data: {
        parentId: item.parenT_ID,
        org: item.core_Org,
        orgType: {
          id: item.core_Org_TYPE?.id,
          ar_Name: item.core_Org_TYPE?.ar_Name,
          en_Name: item.core_Org_TYPE?.en_Name,
        },
        structure: null,
        structureId: orgStructureId,
        numEmp: item.numEmp,
      },
      children: [],
    };
    // root nodes (company)
    if (!item.parenT_ID) {
      orgStructureDetails[0].children.push(mapedItem);
    }
    // other nodes
    else companyMapChildrenData(mapedItem, orgStructureDetails[0].children);
  });

  return orgStructureDetails;
}

// to make sure that all parent nodes come before their children
function companySortDataBeforeMap(
  core_Org_Structure_Org: IOrgStructureNodeModel[],
  sortedOrgNodes: IOrgStructureNodeModel[],
  selectedIds: number[]
) {
  core_Org_Structure_Org.forEach((item) => {
    if (
      (item.parenT_ID == null || selectedIds.includes(item.parenT_ID)) &&
      !selectedIds.includes(item.orG_ID)
    ) {
      sortedOrgNodes.push(item);
      selectedIds.push(item.orG_ID);
    }
  });
}

function companyMapChildrenData(
  mapedItem: TreeNode<ICompanyNodeChartData>,
  chatDataParentArray: TreeNode<ICompanyNodeChartData>[]
) {
  chatDataParentArray.map((lastItem) => {
    if (lastItem.data.org.id == mapedItem.data.parentId) {
      lastItem.children.push(mapedItem);
      return;
    }
  });
  // if not found search in children
  chatDataParentArray.map((childItem) => {
    companyMapChildrenData(mapedItem, childItem.children);
  });
}

/* /////////////////// job title chart /////////////////// */

export function jobTitleChartToMap(
  jobTitles: IJobTitleModel[]
): TreeNode<IJobTitleModel>[] {
  let jobTitleOrgChart = [
    {
      label: jobTitles[0].code,
      type: "job-title",
      styleClass: "job-title-node",
      expanded: true,
      data: jobTitles[0],
      children: [],
    },
  ];
  jobTitleMapChildrenData(jobTitleOrgChart[0]);

  return jobTitleOrgChart;
}

function jobTitleMapChildrenData(parentJobTitle: TreeNode<IJobTitleModel>) {
  parentJobTitle.data.chiledJobTitles?.map((item) => {
    // maping data
    let mapedItem: TreeNode<IJobTitleModel> = {
      label: item.code,
      type: "job-title",
      styleClass: "job-title-node",
      expanded: true,
      data: item,
      children: [],
    };
    parentJobTitle.children.push(mapedItem);
    jobTitleMapChildrenData(mapedItem);
  });
}

/* /////////////////// employee chart /////////////////// */

export function employeeChartToMap(
  employees: IEmployeeCardModel[],
  lastSelectedEmpId
): TreeNode<IEmployeeCardModel>[] {
  let firstEmployeeIndex = employees.findIndex(
    (emp) => emp.id == lastSelectedEmpId
  );

  let resourcesChart: TreeNode<IEmployeeCardModel>[] = [];
  if (firstEmployeeIndex >= 0) {
    resourcesChart = [
      {
        label: msgTranslate(
          employees[firstEmployeeIndex]?.ar_Name,
          employees[firstEmployeeIndex]?.en_Name
        ),
        type: "resources",
        styleClass: employees[firstEmployeeIndex].isSolidEmp
          ? ""
          : "dashed-border",
        expanded: true,
        expandedIcon: "pi pi-folder-open",
        collapsedIcon: "pi pi-folder",
        data: employees[firstEmployeeIndex],
        children: [],
      },
    ];

    // maping data
    employees
      .filter((emp) => emp.id != lastSelectedEmpId)
      .map((item) => {
        let mapedItem: TreeNode<IEmployeeCardModel> = {
          label: msgTranslate(item.ar_Name, item.en_Name),
          type: "resources",
          styleClass: item.isSolidEmp ? "" : "dashed-border",
          expanded: true,
          expandedIcon: "pi pi-folder-open",
          collapsedIcon: "pi pi-folder",
          data: item,
          children: [],
        };
        // root node
        if (item.parentID == lastSelectedEmpId) {
          resourcesChart[0].children.push(mapedItem);
        }
        // other nodes
        else if (item.id != lastSelectedEmpId)
          employeeMapChildrenData(mapedItem, resourcesChart[0].children);
      });
  }

  return resourcesChart;
}

function employeeMapChildrenData(
  mapedItem: TreeNode<IEmployeeCardModel>,
  chatDataParentArray: TreeNode<IEmployeeCardModel>[]
) {
  chatDataParentArray.map((lastItem) => {
    if (lastItem.data.id == mapedItem.data.parentID) {
      lastItem.children.push(mapedItem);
      return;
    }
  });
  // if not found search in children
  chatDataParentArray.map((childItem) => {
    employeeMapChildrenData(mapedItem, childItem.children);
  });
}
