import { Component, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-org-structure-details",
  templateUrl: "./org-structure-details.component.html",
  styleUrls: ["./org-structure-details.component.scss"],
})
export class OrgStructureDetailsComponent
  extends AppHelpers
  implements OnInit
{
  orgStructureId: number = 0;

  constructor() {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.orgStructureId = Number(params.get("orgStructureId")) ?? 0;
    });
  }

  // override onChangeBusinessAndCompanyId(): void {}
}
