import {
  Component,
  HostListener,
  Input,
  OnInit,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { FormGroup } from "@angular/forms";
import { DialogPosition, MatDialog } from "@angular/material/dialog";
import { IOrganizationModel } from "app/org-management-setup/models/organizations";
import { OrganizationsService } from "app/org-management-setup/services/organizations.service";
import {
  ICompanyNodeChartData,
  IOrgStructureModel,
  IOrgStructureNodeDeleteParams,
} from "app/org-management/model/org-structure";
import { OrgStructureService } from "app/org-management/services/org-structure.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { TreeNode } from "primeng/api";
import { AddOrgStructureNodeComponent } from "../add-org-structure-node/add-org-structure-node.component";
import { OrgDetailsEmployeesComponent } from "../org-details-employees/org-details-employees.component";
import { companyChartToMap } from "app/org-management/mapper/org-structure";
import { OrganizationChart } from "primeng/organizationchart";
import {exportChart} from "app/org-management/export_chart";


@Component({
  selector: "app-org-structure-hierarchy-details",
  templateUrl: "./org-structure-hierarchy-details.component.html",
  styleUrls: ["./org-structure-hierarchy-details.component.scss"],
})
export class OrgStructureHierarchyDetailsComponent
  extends AppHelpers
  implements OnInit
{
  @Input() orgStructureId: number;
  @Input() canNotEdit: boolean;

  // all structure hierarchy
  orgStructureDetails: TreeNode<ICompanyNodeChartData>[];
  // selected part of structure hierarchy to show
  orgStructureDetailsFiltered: TreeNode<ICompanyNodeChartData>[] = [];
  // organization dropdown
  organizationList: IOrganizationModel[] = [];

  form: FormGroup;
  loading: boolean = true;
	@ViewChild("companyChart") companyChart: OrganizationChart;

  constructor(
    private orgStructureService: OrgStructureService,
    private organizationsService: OrganizationsService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      organizationTypeId: [null],
      organization_ID: [
        {
          value: null,
          disabled: true,
        },
      ],
    });

    // if (this.orgStructureId > 0 && this.CheckNoBusinessOrCompany()) {
    //   this.getStructureDetails();
    // }

    this.formCtrls.organization_ID.valueChanges.subscribe((orgId) => {
      // if orgId == 0 > don't change any thing
      if (orgId == null || orgId > 0) this.onChangeOrg(orgId);
    });
  }

  firstOnChange: boolean = true;
  ngOnChanges(changes: SimpleChanges): void {
    // console.log(changes);
    if (changes.orgStructureId)
      this.firstOnChange
        ? (this.firstOnChange = false)
        : this.onChangeBusinessAndCompanyId();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.orgStructureId > 0 && this.checkNoBusinessOrCompany()) {
      this.getStructureDetails();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getStructureDetails() {
    // this.showLoading();
    this.orgStructureService
      .getByIdWithHierarchy(this.orgStructureId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrgStructureModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.orgStructureDetails = companyChartToMap(
            res.data[0],
            this.orgStructureId
          );
          this.orgStructureDetailsFiltered = this.orgStructureDetails.slice();
          this.loading = false;
        } else {
          this.errorList(res.message.messages);
          this.loading = false;
        }
        // this.hideLoading();
      });
  }

  // on select organization to start show from it
  onChangeOrg(orgId: number) {
    this.orgStructureDetailsFiltered = [];
    if (orgId > 0) this.getChartDataByOrg(orgId, this.orgStructureDetails[0]);
    else this.orgStructureDetailsFiltered = this.orgStructureDetails;
  }

  getChartDataByOrg(orgId: number, item: TreeNode<ICompanyNodeChartData>) {
    item.children.map((item) => {
      if (item.data.org.id == orgId) {
        this.orgStructureDetailsFiltered = [item];
        setTimeout(() => {
          this.setPositionBtnUpOneLevel();
          this.setPositionUpOneLevelLine();
        }, 50);
        return;
      }
    });
    item.children.map((childItem) => this.getChartDataByOrg(orgId, childItem));
  }

  // up one level in chart
  upOneLevel() {
    this.formCtrls.organization_ID.setValue(0);
    let parentOrgId = this.orgStructureDetailsFiltered[0].data.parentId;
    if (parentOrgId)
      this.getChartDataByOrg(parentOrgId, this.orgStructureDetails[0]);
    // if up from one company node to get all companies
    else this.orgStructureDetailsFiltered = this.orgStructureDetails;
  }

  upOneLevelBtnPositions = {
    btnLeft: 0,
    lineLeft: 0,
  };
  setPositionBtnUpOneLevel() {
    let btnWidth = document
      .querySelector(".upOneLevel-btn")
      .getBoundingClientRect().width;
    let firstNodePositions = document
      .querySelector(".p-organizationchart-node-content")
      ?.getBoundingClientRect();
    let firstNodeContainerPositions = document
      .querySelector(".p-organizationchart")
      ?.getBoundingClientRect();

    this.upOneLevelBtnPositions.btnLeft =
      firstNodePositions?.left -
        firstNodeContainerPositions?.left +
        firstNodePositions?.width / 2 -
        btnWidth / 2 ?? 0;
  }
  setPositionUpOneLevelLine() {
    let firstNodePositions = document
      .querySelector(".p-organizationchart-node-content")
      ?.getBoundingClientRect();
    let firstNodeContainerPositions = document
      .querySelector(".p-organizationchart")
      ?.getBoundingClientRect();

    this.upOneLevelBtnPositions.lineLeft =
      firstNodePositions?.left -
        firstNodeContainerPositions?.left +
        firstNodePositions?.width / 2 ?? 0;
  }

  // delete node
  onDelete(node: TreeNode<ICompanyNodeChartData>): void {
    let msg = this.msgTranslate(node.data.org.ar_Name, node.data.org.en_Name);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.deleteNode(node);
    });
  }

  deleteNode(node: TreeNode<ICompanyNodeChartData>) {
    let params: IOrgStructureNodeDeleteParams = {
      StructureID: this.orgStructureId,
      OrgID: node.data.org.id,
    };
    this.showLoading();
    this.orgStructureService.DeleteNode(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getStructureDetails();
        // update exist organization dropdown after delete organization
        this.getAllOrganizationsByOrgType();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  // add, edit node dialog
  openAddEditDialog(
    node: TreeNode<ICompanyNodeChartData> /* , isAdd: boolean */
  ) {
    return this.dialog
      .open(AddOrgStructureNodeComponent, {
        data: {
          org_Structure_ID: this.orgStructureId,
          parenT_ID: node.data.org?.id ?? null,
          // parenT_ID: isAdd ? node.data.org?.id ?? null : node.data.parentId,
          // orG_TYPE_ID: isAdd ? 0 : node.data.orgType.id,
          // orG_ID: isAdd ? 0 : node.data.org.id,
          // currentOrganization: isAdd ? null : node.data.org,
        },
        minWidth: "50vw",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.getStructureDetails();
          // update exist organization dropdown after add organization
          this.getAllOrganizationsByOrgType();
        }
      });
  }

  // node details dialog
  openOrgDetailsDialog(
    target: HTMLElement,
    nodeData: ICompanyNodeChartData,
    expandedIndex: number
  ) {
    let targetNode: HTMLElement = this.getHeaderElement(target);
    let targetPositions: DOMRect = targetNode.getBoundingClientRect();

    let dialogWidth = 350;
    let dialogPositions: DialogPosition = {};
    // can open left
    if (
      targetPositions.left + targetPositions.width + 10 + dialogWidth <=
      window.innerWidth
    )
      dialogPositions.left = `${
        targetPositions.left + targetPositions.width + 10
      }px`;
    // can open right
    else if (targetPositions.left - dialogWidth - 10 >= 0)
      dialogPositions.left = `${targetPositions.left - dialogWidth - 10}px`;
    // no left and no right
    else dialogPositions.left = null;

    return this.dialog
      .open(OrgDetailsEmployeesComponent, {
        data: {
          org_Structure_ID: this.orgStructureId,
          nodeData: nodeData,
          // 0 > details // 1 >> employees
          expandedIndex: expandedIndex,
        },
        position: dialogPositions,
        width: `${dialogWidth}px`,
        // maxWidth: "350px",
        maxHeight: "90hv",
        // disableClose: true,
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((closed: boolean) => {});
  }

  getHeaderElement(target: HTMLElement): HTMLElement {
    return target.classList.contains("p-organizationchart-node-content")
      ? target
      : this.getHeaderElement(target.parentElement);
  }

  // expand node
  expandNode(node: TreeNode) {
    node.expanded = !node.expanded;
  }


  // organization list
  getAllOrganizationsByOrgType() {
    let orgTypeId: number = this.formCtrls.organizationTypeId.value;
    this.formCtrls.organization_ID.setValue(null);
    if (orgTypeId > 0) {
      // this.showLoading();
      this.organizationsService
        .GetAllByOrgTypeAndStructure(orgTypeId, this.orgStructureId)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<IOrganizationModel[]>) => {
          if ((res.message.messageType = MessageTypes.Success)) {
            this.organizationList = res.data;
          } else this.errorList(res.message.messages);
          // this.hideLoading();
        });
      this.formCtrls.organization_ID.enable();
    } else this.formCtrls.organization_ID.disable();
  }

  // change up one level btn & line positions
  @HostListener("window:resize", ["$event"])
  onWindowResize(event) {
    // this.dialog.closeAll();
    this.setPositionBtnUpOneLevel();
    this.setPositionUpOneLevelLine();
  }

    protected readonly exportChart = exportChart;
}
