import { EmployeeCardComponent } from './../employee-card/employee-card.component';
import { Component, HostListener, OnInit, ViewChild } from "@angular/core";
import { DialogPosition, MatDialog } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { TreeNode } from "primeng/api";
import { FormGroup } from "@angular/forms";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ResourcesChartService } from "app/org-management/services/resources-chart.service";
import { IEmployeeCardModel } from "app/personnel-setup/models/employees";
import { employeeChartToMap } from "app/org-management/mapper/org-structure";
import { OrganizationChart } from 'primeng/organizationchart';
import {exportChart} from "app/org-management/export_chart";

@Component({
  selector: "app-resources-chart",
  templateUrl: "./resources-chart.component.html",
  styleUrls: ["./resources-chart.component.scss"],
})
export class ResourcesChartComponent extends AppHelpers implements OnInit {
  resourcesChart: TreeNode<IEmployeeCardModel>[];

  form: FormGroup;
  loading: boolean = false;
  lastSelectedEmpId: number = 0;
	@ViewChild("resourceChart") resourceChart: OrganizationChart;

  constructor(
    private resourcesChartService: ResourcesChartService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      employeeId: [null],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany())
      this.getResourcesChart(this.formCtrls.employeeId.value);
  }

  get formCtrls() {
    return this.form.controls;
  }

  getResourcesChart(employeeId: number) {
    // console.log(this.form.value);
    if (employeeId > 0) {
      this.lastSelectedEmpId = employeeId;
      this.resourcesChart = null;
      this.loading = true;
      this.resourcesChartService
        .getEmployeesChart(employeeId)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<IEmployeeCardModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.resourcesChart = employeeChartToMap(
              res.data,
              this.lastSelectedEmpId
            );
            setTimeout(() => {
              this.setPositionBtnUpOneLevel();
              this.setPositionUpOneLevelLine();
            }, 100);
            if (this.lastSelectedEmpId == employeeId) this.loading = false;
          } else {
            this.errorList(res.message.messages);
            this.loading = false;
          }
        });
    }
  }

  // on select employee to start show from it
  onChangeEmployee() {
    this.getResourcesChart(this.formCtrls.employeeId.value);
  }

  // up one level in chart
  upOneLevel() {
    // get employees start from parent employee id
    // after change dropdown value > call onChangeEmployee function
    this.formCtrls.employeeId.setValue(this.resourcesChart[0].data.parentID);
    // this.getResourcesChart(this.resourcesChart[0].data.parentID);
  }

  upOneLevelBtnPositions = {
    btnLeft: 0,
    lineLeft: 0,
  };
  setPositionBtnUpOneLevel() {
    let btnWidth = document
      .querySelector(".upOneLevel-btn")
      .getBoundingClientRect().width;
    let firstNodePositions = document
      .querySelector(".p-organizationchart-node-content")
      ?.getBoundingClientRect();
    let firstNodeContainerPositions = document
      .querySelector(".p-organizationchart")
      ?.getBoundingClientRect();

    this.upOneLevelBtnPositions.btnLeft =
      firstNodePositions?.left -
        firstNodeContainerPositions?.left +
        firstNodePositions?.width / 2 -
        btnWidth / 2 ?? 0;
  }
  setPositionUpOneLevelLine() {
    let firstNodePositions = document
      .querySelector(".p-organizationchart-node-content")
      ?.getBoundingClientRect();
    let firstNodeContainerPositions = document
      .querySelector(".p-organizationchart")
      ?.getBoundingClientRect();

    this.upOneLevelBtnPositions.lineLeft =
      firstNodePositions?.left -
        firstNodeContainerPositions?.left +
        firstNodePositions?.width / 2 ?? 0;
  }

  // node details dialog
  openEmployeeDetailsDialog(target: HTMLElement, nodeData: IEmployeeCardModel) {
    let targetNode: HTMLElement = this.getHeaderElement(target);
    let targetPositions: DOMRect = targetNode.getBoundingClientRect();

    let dialogWidth = 350;
    let dialogPositions: DialogPosition = {};
    // can open left
    if (
      targetPositions.left + targetPositions.width / 2 + dialogWidth <=
      window.innerWidth
    )
      dialogPositions.left = `${
        targetPositions.left + targetPositions.width / 2
      }px`;
    // can open right
    else if (
      targetPositions.left - dialogWidth / 2 - targetPositions.width / 2 + 10 >=
      0
    )
      dialogPositions.left = `${
        targetPositions.left - dialogWidth / 2 - targetPositions.width / 2 + 10
      }px`;
    // no left and no right
    else dialogPositions.left = null;

    return this.dialog
      .open(EmployeeCardComponent, {
        data: { employeeId: nodeData.id },
        position: dialogPositions,
        width: `${dialogWidth}px`,
        // maxWidth: "350px",
        maxHeight: "80hv",
        // disableClose: true,
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((closed: boolean) => {});
  }

  getHeaderElement(target: HTMLElement): HTMLElement {
    return target.classList.contains("p-organizationchart-node-content")
      ? target
      : this.getHeaderElement(target.parentElement);
  }

  // expand node
  expandNode(node: TreeNode) {
    node.expanded = !node.expanded;
  }

  // change up one level btn & line positions
  @HostListener("window:resize", ["$event"])
  onWindowResize(event) {
    // this.dialog.closeAll();
    this.setPositionBtnUpOneLevel();
    this.setPositionUpOneLevelLine();
  }

  protected readonly exportChart = exportChart;
}
