import {Component, OnInit} from "@angular/core";
import {MatTableDataSource} from "@angular/material/table";
import {orgStructureToMap} from "app/org-management/mapper/org-structure";
import {IOrgStructureModel} from "app/org-management/model/org-structure";
import {OrgStructureService} from "app/org-management/services/org-structure.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IPageParams} from "app/shared/interfaces/params";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
	selector: "app-org-structure-index",
	templateUrl: "./org-structure-index.component.html",
	styleUrls: ["./org-structure-index.component.scss"],
})
export class OrgStructureIndexComponent extends AppHelpers implements OnInit {
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"en_Name",
		"ar_Name",
		"froM_DATE",
		"tO_DATE",
		"actions",
	];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IOrgStructureModel>;

	constructor(public orgStructureService: OrgStructureService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.removePageParams();
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.orgStructureService
			.getList(params)
      .pipe(this.cancelRequest())
			.subscribe((res: IResponseList<IOrgStructureModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					this.dataSource = new MatTableDataSource(orgStructureToMap(res.data));
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(orgStructure: IOrgStructureModel): void {
		let msg = this.msgTranslate(orgStructure.ar_Name, orgStructure.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(orgStructure);
		});
	}

	delete(orgStructure: IOrgStructureModel) {
		this.showLoading();
		this.orgStructureService.Delete(orgStructure.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
