import { Injectable } from "@angular/core";
import {
  IEmpJobDataModel,
  IEmpJobDataRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeJobInformationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpJobDataRequest) {
    return this.post<IResponse<IEmpJobDataModel>>(
      "Personnel/Employees/Create_JobInfo",
      data
    );
  }

  update(data: IEmpJobDataRequest) {
    return this.post<IResponse<IEmpJobDataModel>>(
      "Personnel/Employees/Edit_JobInfo",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpJobDataModel[]>>(
      "Personnel/Employees/GetAll_JobInfo",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  getAllCurrentJobsByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpJobDataModel[]>>(
      "personnel/Employees/GetAll_Current_Job_Emp",
      {
        params: {
          PerinsID: employeeId,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_JobInfo", {
      params: { Id: Id },
    });
  }
}
