import { db } from "app/shared/resources/database-tabels";
import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IJobTitleModel } from "../model/job-title";

@Injectable({
  providedIn: "root",
})
export class JobTitleChartService extends AppClientService {
  constructor() {
    super();
  }

  getJobTitleHierarchy(jobId: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll_Structure",
      {
        params: {
          $filter: `id eq ${jobId}`,
          $expand: `${db.core_Org_Job_Title}/${db.core_Org},${db.Core_Person_Instance_Job_Title}.${db.Core_Person_Instance}.${db.Core_Person},${db.Core_Job_Title_Family}`,
        },
      }
    );
  }
}
