import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IOrgStructureAddRequest,
  IOrgStructureEditRequest,
  IOrgStructureModel,
} from "app/org-management/model/org-structure";
import { OrgStructureService } from "app/org-management/services/org-structure.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
@Component({
  selector: "app-add-org-structure",
  templateUrl: "./add-org-structure.component.html",
  styleUrls: ["./add-org-structure.component.scss"],
})
export class AddOrgStructureComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  orgStructure: IOrgStructureModel;
  orgStructureId: number;
  oldStructureIdToCopy: number;

  todayDate: Date = new Date();

  structureList: IOrgStructureModel[] = [];

  constructor(private orgStructureService: OrgStructureService) {
    super();
    this.todayDate.setHours(0, 0, 0, 0);
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      froM_DATE: [null, [Validators.required]],
      CopyStructureID: [null],
    });

    this.activatedRoute.paramMap.subscribe((params) => {
      this.orgStructureId = Number(params.get("orgStructureId")) ?? 0;
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.orgStructureId == 0 ? this.getAllStructures() : this.getById();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.orgStructureService
      .getById(this.orgStructureId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrgStructureModel[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.orgStructure = res.data[0];
          this.orgStructureToEdit();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  orgStructureToEdit() {
    this.form.patchValue(this.orgStructure);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      this.formCtrls.id.value == 0 ? this.create() : this.update();
    } // else this.toastr.error("General.formNotValid");
  }

  create() {
    const model: IOrgStructureAddRequest = {
      Core_Org_Structure: {
        en_Name: this.form.value.en_Name,
        ar_Name: this.form.value.ar_Name,
        froM_DATE: HelpersFunctions.mapDateReverse(
          this.form.value.froM_DATE ?? this.orgStructure?.froM_DATE
        ),
      },
      CopyStructureID: this.formCtrls.CopyStructureID.value,
    };

    this.showLoading();
    this.orgStructureService
      .create(model)
      .subscribe((res: IResponse<IOrgStructureModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.orgStructureId = res.data.id;
          this.formCtrls.id.setValue(this.orgStructureId);
          let url = `/org-management/${this.moduleId}/org-structure/edit/${this.orgStructureId}`;
          this.navigateTo(url);
          this.orgStructure = res.data;
          this.formCtrls.CopyStructureID.reset();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update() {
    const model: IOrgStructureEditRequest = {
      id: this.form.value.id,
      en_Name: this.form.value.en_Name,
      ar_Name: this.form.value.ar_Name,
      froM_DATE: HelpersFunctions.mapDateReverse(
        this.form.value.froM_DATE ?? this.orgStructure?.froM_DATE
      ),
      to_DATE: HelpersFunctions.mapDateReverse(this.orgStructure.tO_DATE),
    };

    this.showLoading();
    this.orgStructureService
      .update(model)
      .subscribe((res: IResponse<IOrgStructureModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.orgStructure = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    /* if (this.orgStructureId == 0)  */ this.form.reset({ id: 0 });
    // else this.orgStructureToEdit();
    this.submitted = false;
  }

  canNotEdit(): boolean {
    // can edit if org structure from date greater than today date
    return (
      new Date(this.todayDate).getTime() >=
      new Date(this.orgStructure?.froM_DATE).getTime()
    );
  }

  onChangeOldStructure() {}

  // organization list
  getAllStructures() {
    this.orgStructureService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrgStructureModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.structureList = [
            {
              id: 0,
              ar_Name: "انشاء جديد",
              en_Name: "Create new",
            },
            ...res.data,
          ];
        } else this.errorList(res.message.messages);
      });
  }
}
