import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { OrgStructureIndexComponent } from "./components/org-structure/org-structure-index/org-structure-index.component";
import { AddOrgStructureComponent } from "./components/org-structure/add-org-structure/add-org-structure.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { OrgStructureDetailsComponent } from "./components/org-structure/org-structure-details/org-structure-details.component";
import { JobsProfileIndexComponent } from "./components/jobs-profile/jobs-profile-index/jobs-profile-index.component";
import { AddJobProfileComponent } from "./components/jobs-profile/add-job-profile/add-job-profile.component";
import { AssignJobToOrgComponent } from "./components/jobs-profile/assign-job-to-org/assign-job-to-org.component";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
	{
		path: "org-structure",
		data: { view: ViewsManager.org_structure },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: OrgStructureIndexComponent,
				data: { view: ViewsManager.org_structure_index },
			},
			{
				path: "add",
				component: AddOrgStructureComponent,
				data: { view: ViewsManager.add_org_structure },
			},
			{
				path: "edit/:orgStructureId",
				component: AddOrgStructureComponent,
				data: { view: ViewsManager.add_org_structure },
			},
			{
				path: "details/:orgStructureId",
				component: OrgStructureDetailsComponent,
				data: { view: ViewsManager.org_structure_details },
			},
		],
	},
	{
		path: "job-profile",
		data: { view: ViewsManager.job_profile },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: JobsProfileIndexComponent,
				data: { view: ViewsManager.job_profile_index },
			},
			{
				path: "add",
				component: AddJobProfileComponent,
				data: { view: ViewsManager.add_job_profile },
			},
			{
				path: "edit/:jobId",
				component: AddJobProfileComponent,
				data: { view: ViewsManager.add_job_profile },
			},
			{
				path: "assign-job-to-org",
				component: AssignJobToOrgComponent,
				data: { view: ViewsManager.assign_job_to_org },
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class OrgManagementRoutingModule {}
