import { Component, HostListener, Inject, OnInit } from "@angular/core";
import { DialogPosition, MAT_DIALOG_DATA, MatDialog, MatDialogRef } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { GroupByService } from "app/shared/services/group-by.service";
import { EmployeeCardComponent } from "../employee-card/employee-card.component";
import { ICompanyNodeChartData } from "app/org-management/model/org-structure";
import { IPerson } from "app/personnel-setup/models/employees";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";

@Component({
	selector: "app-org-details-employees",
	templateUrl: "./org-details-employees.component.html",
	styleUrls: ["./org-details-employees.component.scss"],
})
export class OrgDetailsEmployeesComponent extends AppHelpers implements OnInit {
	nodeData: ICompanyNodeChartData;
	expandedIndex: number = 0;
	employees: IPerson[] = [];
	employeesGroups: {
		key: string;
		List: any[];
	}[];
	employeesGroupsFiltered: {
		key: string;
		List: any[];
	}[];
	loaded: boolean = false;
	constructor(
		private employeesService: EmployeesService,
		private groupByService: GroupByService,
		// opened ass dialog
		public dialogRef: MatDialogRef<OrgDetailsEmployeesComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		// to open another dialog
		private dialog: MatDialog
	) {
		super();
		this.expandedIndex = data.expandedIndex;
		this.nodeData = data.nodeData;
	}

	ngOnInit() {
		if (
			this.checkNoBusinessOrCompany() &&
			this.nodeData.structureId > 0 &&
			this.nodeData.org.id > 0
		)
			this.getEmployeesByOrgId();

		const overlays = document.querySelectorAll(".cdk-overlay-backdrop");
		if (overlays.length > 0) overlays[overlays.length - 1].classList.add("no-overlay");
	}

	getEmployeesByOrgId() {
		this.employeesService
			.getAllByStructureAndOrgId(this.nodeData.structureId, this.nodeData.org.id)
			.pipe(this.cancelRequest())
			.subscribe((res: IResponse<IPerson[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employees = res.data;
					if (this.employees.length > 0) this.sortAndGroupEmployees();
					else this.loaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	sortAndGroupEmployees() {
		this.employeesGroups = this.groupByService
			.groupByPartOfProperty(
				this.employees,
				this.languageService.isArabic ? "ar_Name" : "en_Name",
				1
			)
			.sort((a, b) => {
				if (a.key < b.key) return -1;
				if (a.key > b.key) return 1;
				return 0;
			});
		this.employeesGroupsFiltered = this.employeesGroups.slice();
		this.loaded = true;
	}

	// open employee card dialog
	openEmployeeCardDialog(target: HTMLElement, employeeId: number) {
		let lastTarget: HTMLElement = this.getHeaderElement(target);
		let targetPositions: DOMRect = lastTarget.getBoundingClientRect();

		// dialog position x-axis (right or left)
		let dialogWidth = 350;
		let dialogPositions: DialogPosition = {};
		// can open left
		if (targetPositions.left + targetPositions.width / 2 + dialogWidth <= window.innerWidth)
			dialogPositions.left = `${targetPositions.left + targetPositions.width / 2}px`;
		// can open right
		else if (targetPositions.left - dialogWidth / 2 - 10 >= 0)
			dialogPositions.left = `${targetPositions.left - dialogWidth / 2 - 10}px`;
		// no left and no right
		else dialogPositions.left = null;

		return this.dialog
			.open(EmployeeCardComponent, {
				data: { employeeId: employeeId },
				position: dialogPositions,
				width: `${dialogWidth}px`,
				// maxWidth: "350px",
				maxHeight: "80hv",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((closed: boolean) => {});
	}

	getHeaderElement(target: HTMLElement): HTMLElement {
		return target.classList.contains("mat-expansion-panel-body")
			? target
			: this.getHeaderElement(target.parentElement);
	}

	// search in employees
	searchEmployees(searchName: string) {
		this.employeesGroupsFiltered = this.employeesGroups
			.map((item) => ({
				key: item.key,
				List: item.List.filter(
					(emp) =>
						emp.en_Name.toLowerCase().includes(searchName.toLowerCase()) ||
						emp.ar_Name.toLowerCase().includes(searchName.toLowerCase())
				),
			}))
			.filter((item) => item.List.length > 0);
	}

	// close dilog on resize screen
	@HostListener("window:resize", ["$event"])
	onWindowResize(event) {
		this.dialogRef.close();
	}
}
